// client/src/main-view/main-view.scss

.main-view {
    margin-left: auto;
    margin-right: auto;
    width: 80%;
    margin-bottom: 0;
}

.card {
    margin-top: 10px;
    margin-bottom: 75px;
    min-height: 200px;
    box-shadow: 0 2px 10px 0 rgba(134, 141, 155, 1);
}

.card:hover {
    cursor: pointer;
    box-shadow: none;
}

.navbar {
    font-family: 'Merriweather', serif;
    font-size: 20px;
    background-color: rgba(205, 243, 250, 1);
}

.navbar-brand {
    font-size: 30px;
  }

  .profile-button, .logout-button {
    border-radius: 24px; 
  }

.borderBottom {
    background-color: rgba(184, 195, 204, 1);
    min-height: 10px;
}